@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);
body {
    background-color: #111;
	font-family: 'Oxygen', sans-serif;
}

[owner], p, button, input[type='range'], #selectedColor {
    cursor: pointer;
}

#warning {
    background-color: var(--main-bg-color);
    padding: 50% 0px;
    text-align: center;
}
.grid {
    margin: 0 auto;
    background-color: white;
}

.cellcanvas {
    border: 1px solid black;
}

.cellgridSavedColors {
    border: 1px solid black;
    background-color: #eee;
    width: 15px;
    height: 15px;
}

table {
   border-collapse: collapse;
}
.dropdown {
    position: relative;
    display: inline-block;
    width: 100%;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown-content {
    display: none;
    position: absolute;
    width: 90%;
    z-index: 1;
	background-color: #222;
    color: white;
    padding: 5%;
    border-radius: 5%;
  }
  
#buttonSave {
	font-size: 15px;
    border-radius: 0%;
    margin-top: 5px;
}

h5 {
	text-align: center;
}

.range {
	padding: 0px 5px;
}

#selectedColor {
	margin: 0px 5px 0px 5px;
	border: 1px solid black;
}

.inputRange {
	width: 100%;
}

.labelRange {
	font-weight: bolder;
	margin: 10px 5px;
	font-size: 13px;
}

.labelRangeValue {
	font-weight: bolder;
	margin: 10px 5px;
	font-size: 13px;
}


:root {
	--main-bg-color: rgba(6, 84, 173, 0.9);
	--main-txt-color: black;
}

/* The sidebar menu */
.sidenav {
	height: 100%;
	/* Full-height: remove this if you want "auto" height */
	width: 25%;
	/* Set the width of the sidebar */
	position: fixed;
	/* Fixed Sidebar (stay in place on scroll) */
	z-index: 1;
	/* Stay on top */
	top: 0;
	/* Stay at the top */
	left: 0;
	background-color: #eee;
	color: black;
	color: var(--main-txt-color);
	/* Black */
	overflow-x: hidden;
	padding: 0px 10px;
}


/* The navigation menu links */

.sidenav p {
	text-align: center;
	font-weight: bold;
	background-color: #222;
	color: whitesmoke;
}


/* Style page content */

.main {
	margin-left: 22%;
	/* Same as the width of the sidebar */
}


/* On smaller screens, where height is less than 450px, change the style of the sidebar (less padding and a smaller font size) */

@media screen and (max-height: 450px) {
	.sidenav {
		padding-top: 15px;
	}
	.sidenav p {
		font-size: 18px;
	}
}


/* SIDENAV */
button {
	background-color: none;
	padding: 10px;
	border: none;
	font-size: 20px;
}

button:hover {
	background-color: rgba(6, 84, 173, 0.9);
	background-color: var(--main-bg-color);
	color: white;
}

button:focus {
    outline:0;
}
